<template>
    <div class="main-page">
        <div class="headerTag">新闻列表</div>

        <van-tabs v-model="active" @change="change" v-if="classList.length" title-active-color="#409EFF" line-width="60px" color="#409EFF">
            <van-tab :title="item.ClassName" v-for="(item, index) in classList" :key="index" :name="item.Id" title-style="font-size:14px;font-weight:600;">
                <ScrollView ref="ScrollView" top="88px" bottom="0" @down="down" @upFun="up">
                    <!--内容...-->
                    <list-line v-for="(item, index) in content()" :key="index" :item="item" :diynew=true />
                    <!-- <list-line :diy= false :new= true v-for="(item, index) in content()" :key="index">
            <template #diy>
              <div class="listcard shadow">
                <div class="show">
                  <img class="showImg" v-lazy="item.img" />
                </div>
                <div class="right">
                  <div class="title hidden-two">{{ item.title }}</div>
                  <div class="time-wrap">
                    <i class="iconfont icon-status-leave"></i>
                    {{ item.CreateTime }}
                  </div>
                </div>
              </div>
            </template>
          </list-line> -->
                </ScrollView>
            </van-tab>
        </van-tabs>
    </div>
</template>
			
<script>
import ListLine from "@/components/ListLine.vue";
export default {
    components: { ListLine },
    data() {
        return {
            name: "7a956b2c-5615-4486-991b-9329bb29f406",
            active: 2,
            classList: [],
            content() {
                return this.dataList.map((item) => {
                    return {
                        img: item.CoverImg,
                        title: item.Title,
                        CreateTime: item.CreateTime,
                        id: item.Id,
                    };
                });
            },
            dataList: [],
            searchInfo: {},
        };
    },
    methods: {
        change(name, title) {
            this.name = name;
            this._api
                .CMSContentIndex({
                    page: 1,
                    pageSize: 20,
                    ClassId: name,
                })
                .then((res) => {
                    this.dataList = res.data;
                });
        },
        async CMSClassIndex() {
            await this._api.CMSClassIndex().then((res) => {
                console.log(res, 222)
                this.classList = res.data;
            });
        },
        down(instance) {
            this.$refs["ScrollView"].reUp();
        },
        // 上拉加载
        up([page, instance]) {
            this._api
                .CMSContentIndex({
                    ...this.searchInfo,
                    page: page.num,
                    pageSize: page.size,
                    ClassId: this.name,
                })
                .then((res) => {
                    if (res) {
                        let arr = res.data;
                        if (page.num === 1) this.dataList = [];
                        this.dataList = this.dataList.concat(arr);
                        this.$nextTick(() => {
                            instance.endSuccess(arr.length);
                        });
                    }
                    this.loading = false;
                });
        }
    },
    mounted() {
        this.CMSClassIndex();
    },
};
</script>
			
<style lang="scss" scoped>
.main-page {
    .listcard {
        width: calc(100% - 30px);
        display: flex;
        justify-content: space-between;
        height: 208rpx;
        padding: 9px;
        margin: 10px 15px;
        .show {
            width: 100px;
            height: 80px;

            .showImg {
                width: 100%;
                height: 100%;
                border-radius: 4px;
            }
        }
        .right {
            width: 65%;
            margin-left: 30rpx;
            line-height: 30rpx;

            .title {
                line-height: 52rpx;
                font-size: 17px;
                color: #303133;
                font-weight: 600;
                margin-bottom: 6px;

                text {
                    font-weight: 600;
                    font-size: 32rpx;
                }
            }

            .time-wrap {
                display: flex;
                line-height: 30px;
                color: #666;
                font-size: 15px;

                i {
                    margin-right: 6px;
                    font-size: 14px;
                }
            }
        }
    }
}
</style>